<template>
  <div class="body-container">
    <add-component v-if="isTableShow&&!isCheckPage" @table-event="tableEvent"></add-component>
    <header-component v-if="isTableShow" @header-event="headerEvent" :is-checkpage="isCheckPage"></header-component>
    <table-component v-if="isTableShow" :data-list="dataList" @table-event="tableEvent" :is-checkpage="isCheckPage"></table-component>
    <footer-component v-if="isTableShow" :data-total="dataTotal" :current-page="pageIndex" :page-size="pageSize" @footer-event="footerEvent"></footer-component>
    <tips-Component v-if="isTipsShow" :is-add="isAdd" :base-data="baseData" @tips-event="tipsEvent" :is-detailpage="isDetailPage" :is-checkpage="isCheckPage"></tips-Component>
  </div>
</template>

<script>
import addComponent from "./components/AddComponent.vue"
import tableComponent from "./components/TableComponent.vue";
import headerComponent from "./components/HeaderComponent.vue";
import footerComponent from "./components/FooterComponent.vue";
import tipsComponent from "./components/TipsComponent.vue";

export default {
  name: "app",
  components: {
    addComponent,
    tableComponent,
    headerComponent,
    footerComponent,
    tipsComponent
  },
  data() {
    return {
      isTipsShow: false,
      isAdd: false,
      isDetailPage: false,
      isTableShow: true,
      isCheckPage: false,
      dataList: [
        // {
        //   "tipsId":1,
        //   "icon":"http://xxxx.xxx",
        //   "name":"双十二tips",
        //   "versionCode":0,
        //   "status":0,
        //   "startTime":"2018-11-21 00:00:00",
        //   "endTime":"2018-11-22 00:00:00",
        //   "modifier":"李军"
        // },
        // {
        //   "tipsId":2,
        //   "icon":"http://xxxx.xxx",
        //   "name":"双十一tips",
        //   "versionCode":0,
        //   "status":1,
        //   "startTime":"2018-11-21 00:00:00",
        //   "endTime":"2018-11-22 00:00:00",
        //   "modifier":"李军"
        // },
        // {
        //   "tipsId":1,
        //   "icon":"http://xxxx.xxx",
        //   "name":"双十二tips",
        //   "versionCode":0,
        //   "status":2,
        //   "startTime":"2018-11-21 00:00:00",
        //   "endTime":"2018-11-22 00:00:00",
        //   "modifier":"李军"
        // },
        // {
        //   "tipsId":2,
        //   "icon":"http://xxxx.xxx",
        //   "name":"双十一tips",
        //   "versionCode":0,
        //   "status":3,
        //   "startTime":"2018-11-21 00:00:00",
        //   "endTime":"2018-11-22 00:00:00",
        //   "modifier":"李军"
        // },
        // {
        //   "tipsId":1,
        //   "icon":"http://xxxx.xxx",
        //   "name":"双十二tips",
        //   "versionCode":0,
        //   "status":4,
        //   "startTime":"2018-11-21 00:00:00",
        //   "endTime":"2018-11-22 00:00:00",
        //   "modifier":"李军"
        // },
        // {
        //   "tipsId":2,
        //   "icon":"http://xxxx.xxx",
        //   "name":"双十一tips",
        //   "versionCode":0,
        //   "status":5,
        //   "startTime":"2018-11-21 00:00:00",
        //   "endTime":"2018-11-22 00:00:00",
        //   "modifier":"李军"
        // },
        // {
        //   "tipsId":1,
        //   "icon":"http://xxxx.xxx",
        //   "name":"双十二tips",
        //   "versionCode":0,
        //   "status":6,
        //   "startTime":"2018-11-21 00:00:00",
        //   "endTime":"2018-11-22 00:00:00",
        //   "modifier":"李军"
        // },
        // {
        //   "tipsId":2,
        //   "icon":"http://xxxx.xxx",
        //   "name":"双十一tips",
        //   "versionCode":0,
        //   "status":0,
        //   "startTime":"2018-11-21 00:00:00",
        //   "endTime":"2018-11-22 00:00:00",
        //   "modifier":"李军"
        // }
      ],
      pageIndex: 1,
      pageSize: 20,
      dataTotal: 0,
      searchName: "",
      searchModifyUser: "",
      searchType: "",
      searchState: "",
      searchField: "",
      searchOrder: "",

      baseData: {}
    };
  },
  methods: {
    //活动管理
    tipsEvent(event) {
      switch (event.type) {
        case "home-page":
          this.getDataXhr();
          this.isTipsShow = false;
          this.isTableShow = true;
          break;
        case "data-page":
          //跳转大数据页面
          break;
        default:
          break;
      }
    },
    //表格事件
    tableEvent(event) {
      switch (event.type) {
        case "data-page":
          //跳转数据页面
          break;
        case "add-page":
          this.baseData = {};
          this.isTableShow = false;
          this.isAdd = true;
          this.isDetailPage = false;
          this.isCheckPage = false;
          this.isTipsShow = true;
          break;
        case "edit-page":
          this.baseData = event.data;
          this.isAdd = false;
          this.isTableShow = false;
          this.isDetailPage = false;
          this.isCheckPage = false;
          this.isTipsShow = true;
          break;
        case "detail-page":
          this.baseData = event.data;
          this.isAdd = false;
          this.isTableShow = false;
          this.isDetailPage = true;
          this.isCheckPage = false;
          this.isTipsShow = true;
          break;
        case "check-page":
          this.baseData = event.data;
          this.isAdd = false;
          this.isTableShow = false;
          this.isDetailPage = false;
          this.isCheckPage = true;
          this.isTipsShow = true;
          break;
        case "refresh-data":
          this.getDataXhr();
          break;
        case "sortChange":
          this.searchOrder = event.data.order;
          this.searchField = event.data.field;
          this.getDataXhr();
          break;
        default:
          break;
      }
    },
    //页头事件
    headerEvent(event) {
      this.searchData(event.data);
    },
    //搜索请求
    searchData(data) {
      this.searchName = data.name;
      this.searchType = data.type;
      this.searchState = data.status;
      this.searchModifyUser = data.modifyUserName;
      this.pageIndex = 1;
      this.getDataXhr();
    },
    //获取数据xhr
    getDataXhr() {
      var param = {
        name: this.searchName === "" ? undefined : this.searchName,
        modifier: this.searchModifyUser === "" ? undefined : this.searchModifyUser,
        status: this.searchState === "" ? undefined : this.searchState,
        field: this.searchField === "" ? undefined : this.searchField,
        order: this.searchOrder === "" ? undefined : this.searchOrder,
        page: this.pageIndex,
        size: this.pageSize
      };
      this.$http
        .post("/scene/getTipsList", param)
        .then(response => {
          this.dataTotal =
            response.data.data && response.data.data.totalElements;
          this.dataList = response.data.data && response.data.data.content;
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //页脚事件
    footerEvent(event) {
      switch (event.type) {
        case "pageChange":
          this.pageIndex = event.data;
          this.getDataXhr();
          break;
        case "sizeChange":
          this.pageIndex = 1;
          this.pageSize = event.data;
          this.getDataXhr();
          break;
        default:
          break;
      }
    }
  },
  created: function () {
    if (this.isCheckPage) {
      this.searchState = 3;
    }
    this.getDataXhr();
  }
};
</script>

<style scoped>
.body-container {
  min-height: 100%;
  padding-left: 70px;
  padding-right: 70px;
}
</style>
