          <a class='download' href='http://test-bj-ws.vivo.com.cn/Zlf73fDGXFlrC6ST/2019010315/1f29f8d9e892a32a9aa7ca04b158b054/5c8904df-25a4-499e-9ac8-d02afee86dde.zip' download="下载" title="下载" target="_blank">此处</a>
          <span> 查看</span>
        </div>
      </div>
    </el-dialog>
    <el-dialog title="禁用申请" :visible.sync="jinyongDialogVisible" width="600px" :before-close="jinyongDialogClose" center>
      <el-form :model="baseData" ref="jinyongData" label-width="100px">
        <el-form-item label=" 禁用原因" prop="reason" :rules="applyRules">
          <el-input v-model="baseData.reason" type="textarea" :autosize="{ minRows: 2, maxRows: 3}" placeholder="请填写禁用原因" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{baseData.reason?baseData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="jinyongDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="jinyongDialogClose()">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="下架申请" :visible.sync="xiajiaDialogVisible" width="600px" :before-close="xiajiaDialogClose" center>
      <el-form :model="baseData" ref="xiajiaData" label-width="100px">
        <el-form-item label="下架原因" prop="reason" :rules="applyRules">
          <el-input v-model="baseData.reason" :autosize="{ minRows: 2, maxRows: 3}" type="textarea" placeholder="请填写下架原因" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{baseData.reason?baseData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="xiajiaDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="xiajiaDialogClose()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import global from "../service/global";

export default {
  name: "tips-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    },
    isAdd: {
      type: Boolean,
      default: false
    },
    isDetailpage: {
      type: Boolean,
      default: false
    },
    baseData: {}
  },
  data() {
    var specialValidate = (rule, value, callback) => {
      if (/^\s+$/.test(value)) {
        callback(new Error("不能输入全空格"));
      } else {
        callback();
      }
    };
    var validateNumber = (rule, value, callback) => {
      if (!/^[0-9]*$/.test(value)) {
      callback(new Error('请输入整数'));
      }
      if(value < 1 || value > 50) {
      callback(new Error('输入区间1~50'));
      }
      callback();
    };
    return {
      approvalData: {},
      nowStatus: '',
      grayscaleDataTypes: [],
      excelFileName: "",
      excelFile: "",
      uploadLoading: false,
      controlType: 1,
      grayscaleType: 1,
      downloadFileName: '',
      downloadFileUrl: '',
      isMantissaList1: false,
      isMantissaList2: false,
      imeiList: [],
      imeiValue: "",
      imeiData: {
        grayscaleDataType: '',
        releaseType: ''
      },
      mantissaList1: [],
      mantissaList2: [],
      isNoticeTimeError: false,
      isTimeError1: false,
      isTimeError2: false,
      startTimeDisabled: false,
      testCardUrl: "",
      isExam: false,
      xiajiaDialogVisible: false,
      jinyongDialogVisible: false,
      isEditPage: true,
      examDialogVisible: false,
      testImgSrc: "",
      testDialogVisible: false,
      xiajiaReason: "",
      jinyongReason: "",
      bohuiReason: "",
      noticeData: {
        title: "",
        content: "",
        startType: "",
        endType: "",
        endTime: "",
        isAllowNotice: 1,
        timeSwitch: false
      },
      btnData: {
        linkType: 2
      },
      noticeDialogVisible: false,
      btnDialogVisible: false,
      contentData: {
        icon: '',
        time: [],
        priority: '',
        content: ''
      },
      textLinkData: {

      },
      nameData: {
        name: ''
      },
      cardPreview: "",
      expandPreview: "",
      holidayList: global.holidayList,
      stepNumber: 1,
      basicData: {
        peopleLabels: [],
        bindType: 2
      },
      timeData: {
        type: 0,
        startTime: "",
        endTime: "",
        holiday: ""
      },
      textLinkRules: {
        linkType: [
          { required: true, message: "请选择文字链接", trigger: "blur" },
        ],
        appPackageName: [
          { type: 'string', pattern:  /^[^\u4e00-\u9fa5]+$/, message: '包名不能含有中文', trigger: "blur" },
        ],
        appMinVersion: [
          { type: 'string',pattern:  /^[0-9]*$/, message: '版本号须为整数', trigger: "blur" },
        ]
      },
      contentRules: {
        time: [
          { required: true, message: "请选择有效时间", trigger: "blur" },
        ],
        priority: [
          { required: true, message: "请设置优先级", trigger: "blur" },
          { required: true, validator: validateNumber, trigger: "blur" }
        ],
        displayText: [
          { required: true, message: "请输入显示内容", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ],
        iconUrl: [
          { required: true, message: "请上传icon", trigger: "blur" },
        ],
      },
      btnRules: {
        buttonName: [
          { required: true, message: "请输入按钮名称", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" },
          // { pattern:  /^[0-9]*$/, message: '请输入非中文', trigger: "blur" },
        ],
        appMinVersion: [
          { ype: 'string',pattern:  /^[0-9]*$/, message: '版本号须为数字', trigger: "blur" },
        ],
        appPackageName: [
          { type: 'string', pattern:  /^[^\u4e00-\u9fa5]+$/, message: '包名不能含有中文', trigger: "blur" },
        ],
      },
      nameRules: {
        name: [
          { required: true, message: "请输入名称", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ],
      },
      noticeRules: {
        title: [
          { required: true, message: "请输入通知标题", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ],
        content: [
          { required: true, message: "请输入通知内容", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ],
        startType: [
          { required: true, message: "请选择出现时间", trigger: "change" },
        ],
        disappearType: [
          { required: true, message: "请选择消失时间", trigger: "change" },
        ]
      },
      applyRules: [
        { required: true, message: "内容不能为空", trigger: "blur" },
        { required: true, validator: specialValidate, trigger: "blur" }
      ]
    };
  },
  methods: {
    //上传icon
    handleIconSuccess(res, file) {
      this.contentData.iconUrl = URL.createObjectURL(file.raw);
    }, 
    beforeIconUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isPNG = file.type === 'image/png';
      const isLt50k = file.size / 1024 < 51;

      if (!isJPG&&!isPNG) {
        this.$message.error('图片不符合规范，请检查后重新上传');
      }
      if (!isLt50k) {
        this.$message.error('图片不符合规范，请检查后重新上传');
      }
      return (isJPG||isPNG) && isLt50k;
    },
    uploadIcon(data) {
      let param = new FormData();
      param.append("tipsId", 'tips_2');
      param.append("iconFile", data.file);
      this.uploadLoading = true;
      this.$http
        .post("/scene/uploadTipsIcon", param)
        .then(response => {
          if(response.data.code===0){
            this.contentData.iconUrl = response.data.data && response.data.data;
            this.contentData = JSON.parse(JSON.stringify(this.contentData))
            this.uploadLoading = false;
          }else {
            this.uploadLoading = false;
            this.$message.error(response.data.msg);
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },

    numberChange(value) {
      if (/^\d+$/.test(value)) {
        let number = Number(value);
        if (number >= 1 && number <= 10080) {
          this.isNoticeTimeError = false;
        } else {
          this.isNoticeTimeError = true;
        }
      } else {
        this.isNoticeTimeError = true;
      }
    },
    dateChange(isStart) {
      let startDate = Date.parse(this.contentData.time[0]);
      let endDate = Date.parse(this.contentData.time[1]);
      let now = Date.now();
      if (startDate < now) {
        this.isTimeError2 = true;
        this.isTimeError1 = false;
      } else if (endDate < now) {
        this.isTimeError2 = true;
        this.isTimeError1 = false;
      } else if (startDate >= endDate) {
        this.isTimeError1 = true;
        this.isTimeError2 = false;
      } else {
        this.isTimeError1 = false;
        this.isTimeError2 = false;
      }
    },
    noticeRadio2Choose() {
      this.noticeData.disappearTime = 120;
      this.noticeData = JSON.parse(JSON.stringify(this.noticeData))
    },
    //删除操作
    handleDelete() {
      console.log('shanchu')
      let message = "是否确定删除“" + this.baseData.name + "”的草稿？";
      this.$confirm(message, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          let param = {
            tipsId: this.baseData.tipsId,
            versionCode: this.baseData.versionCode
          };
          this.$http
            .post("/scene/delTips", param)
            .then(response => {
              if (response.data && response.data.code == "0") {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                this.$emit("tips-event", {
                  type: "home-page"
                });
              } else {
                if (response.data.msg) {
                  this.$message.error(response.data.msg);
                } else {
                  this.$message.error("删除失败！");
                }
              }
            })
            .catch(() => {
              this.$message.error("删除失败！");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    //下架弹窗
    xiajiaDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.xiajiaDialogVisible = false;
      } else {
        this.$refs["xiajiaData"].validate(valid => {
          if (valid) {
            let param = {
              tipsId: this.baseData.tipsId,
              versionCode: this.baseData.versionCode,
              pulloffReason: this.baseData.reason
            };
            this.$http
              .post("/scene/pulloffTips", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: "下架成功"
                  });
                  this.xiajiaReason = this.baseData.reason;
                  this.baseData.reason = "";
                  this.baseData.status = 12;
                  this.xiajiaDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("下架失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("下架失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //下架操作
    xiajia() {
      this.xiajiaDialogVisible = true;
    },
    //上架操作
    shangjia() {
      let param = {
        tipsId: this.baseData.tipsId,
        versionCode: this.baseData.versionCode,
      };
      this.$http
        .post("/scene/putawayTips", param)
        .then(response => {
          if (response.data && response.data.code == "0") {
            this.baseData.status = 9;
            this.$message({
              type: "success",
              message: "上架成功"
            });
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("上架失败！");
            }
          }
        })
        .catch(() => {
          this.$message.error("上架失败！");
        });
    },
    //禁用弹窗
    jinyongDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.jinyongDialogVisible = false;
      } else {
        this.$refs["jinyongData"].validate(valid => {
          if (valid) {
            let param = {
              tipsId: this.baseData.tipsId,
              versionCode: this.baseData.versionCode,
              disableReason: this.baseData.reason
            };
            this.$http
              .post("/scene/disableTips", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: "禁用成功"
                  });
                  this.jinyongReason = this.baseData.reason;
                  this.baseData.reason = "";
                  this.baseData.status = -1;
                  this.jinyongDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("禁用失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("禁用失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //禁用操作
    jinyong() {
      this.jinyongDialogVisible = true;
    },
    //预览测试操作
    previewTest() {
      let url = location.origin + "/scene/getQrCode?url=" + this.testCardUrl;
      this.testImgSrc = url;
      this.testDialogVisible = true;
    },
    //跳转数据页面
    goDataPage() {
      this.$emit("tips-event", {
        type: "data-page"
      });
    },
    //跳转编辑页面
    goEditPage() {
      let endTime = Date.parse(this.baseData.endTime);
      if (this.baseData.status !== 0 && Date.now() > endTime) {
        this.$confirm("活动已结束, 无法编辑！", "提示", {
          confirmButtonText: "确定",
          showCancelButton: false,
          type: "warning",
          center: true
        });
      } else {
        this.stepNumber = 1;
        this.isEditPage = true;
        this.isCheckpage = false;
        this.isDetailpage = false;
      }
    },
    //删除通知数据
    deleteNotice() {
      this.noticeData = {
        title: "",
        content: "",
        startType: "",
        endType: "",
        endTime: "",
        timeSwitch: false
      };
      this.noticeDialogVisible = false;
    },
    //打开添加通知弹窗
    addNoticeButton() {
      this.noticeDialogVisible = true;
    },
    //删除按钮
    deleteBtn() {
      this.btnData = {
        linkType: 2
      };
      this.btnDialogVisible = false;
    },
    //打开添加按钮
    addBtn() {
      this.btnDialogVisible = true;
    },
    //回到列表页面
    goHome() {
      this.$emit("tips-event", {
        type: "home-page"
      });
    },
    isEmpty(key) {
      if (typeof key === 'undefined' || key === '' || key === null) {
        return true;
      } else {
        return false;
      }
    },
    dataValidate (canEmpty) {
      let nflag, cflag, tflag, bflag, noflag = false;
      this.$refs['nameData']&&this.$refs['nameData'].validate((valid) => {
        nflag = valid
      });
      this.$refs['contentData'].validate((valid) => {
        cflag = valid
      });
      this.$refs['textLinkData'].validate((valid) => {
        tflag = valid
      });
      bflag = !this.btnDialogVisible;
      this.btnDialogVisible&&this.$refs['btnData'].validate((valid) => {
        bflag = valid
      });
      noflag = !this.noticeDialogVisible;
      this.noticeDialogVisible&&this.$refs['noticeData'].validate((valid) => {
        noflag = valid
      });
      // 文字有链接时三选一必填
      if(this.textLinkData.linkType===2&&!(this.textLinkData.appPackageName||this.textLinkData.cpFastLink||this.textLinkData.cpH5Link)){
        this.$confirm("跳转链接至少填一种！", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
        return false;
      }
      if(this.textLinkData.linkType===2&&this.textLinkData.appPackageName&&!this.textLinkData.appMinVersion){
        this.$confirm("跳转至应用包名则最低版本号必填！", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
        return false;
      }
      if(this.textLinkData.linkType===2&&this.textLinkData.appPackageName&&!(this.textLinkData.action||this.textLinkData.AppInterfaceClassName||this.textLinkData.url)){
        this.$confirm("action、应用界面类名、url至少填一种！", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
        return false;
      }
      // 按钮
      if(this.btnDialogVisible&&!(this.btnData.appPackageName||this.btnData.cpFastLink||this.btnData.cpH5Link)){
        this.$confirm("跳转链接至少填一种！", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
        return false;
      }
      if(this.btnDialogVisible&&this.btnData.appPackageName&&!this.btnData.appMinVersion){
        this.$confirm("跳转至应用包名则最低版本号必填！", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
        return false;
      }
      if(this.btnDialogVisible&&this.btnData.appPackageName&&!(this.btnData.action||this.btnData.AppInterfaceClassName||this.btnData.url)){
        this.$confirm("action、应用界面类名、url至少填一种！", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
        return false;
      }
      return nflag&&cflag&&tflag&&bflag&&noflag;
    },
    //保存草稿
    draftButton() {
      if (this.dataValidate(true)) {
        this.prepareEditData(0);
      }
    },
    //保存操作
    saveButton() {
      this.prepareEditData(1);
    },
    //准备草稿或者编辑数据
    async prepareEditData(status) {
      let currentState = 0;
      if (this.stepNumber === 2) {
        currentState = 3;
      }
      let param = {
        tipsId: this.baseData.tipsId,
        status: status,
        versionCode: this.baseData.versionCode,
        name: this.nameData.name,
        startTime: this.contentData.time[0],
        endTime: this.contentData.time[1],
        priority: this.contentData.priority,
        iconUrl: this.contentData.iconUrl,
        displayText: this.contentData.displayText,
        textLink: this.textLinkData,
        buttonName: this.btnDialogVisible?this.btnData.buttonName:'',
        buttonLink: this.btnDialogVisible?this.btnData:undefined,
        notice: this.noticeDialogVisible?this.noticeData:undefined
      };
      // if ((param.imeiData.fileName && param.imeiData.fileId) || !param.imeiData.fileName) {
        if(this.isAdd){
          this.addDataXhr(param);
        }else{
          this.editDataXhr(param);
        }

    },
    //上传excel文件
    async uploadExcel() {
      let formData = new FormData();
      formData.append("tipsId", this.baseData.id);
      formData.append("imeiFile", this.excelFile);
      return new Promise((resolve) => {
        this.$http
          .post('/scene/upoloadGreyImeiFile', formData)
          .then(response => {
            if (response.data && response.data.code == 0) {
              let fileId = response.data.data;
              resolve(fileId);
            } else {
              this.uploadLoading = false;
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("操作失败！");
              }
              resolve('');
            }
          })
          .catch(() => {
            this.uploadLoading = false;
            this.$message.error("操作失败！");
            resolve('');
          });
      });
    },
    //保存新增数据xhr
    addDataXhr(param) {
      this.$http
        .post("/scene/addTips", param)
        .then(response => {
          this.uploadLoading = false;
          if (response.data && response.data.code == "0") {
            if (this.stepNumber === 1) {
              this.$message({
                type: "success",
                message: "草稿保存成功"
              });
            } else {
              this.stepNumber++;
            }
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("操作失败！");
            }
          }
        })
        .catch(() => {
          this.uploadLoading = false;
          this.$message.error("操作失败！");
        });
    },
    // 提交编辑
    editDataXhr(param) {
      this.$http
        .post("/scene/modifyTips", param)
        .then(response => {
          this.uploadLoading = false;
          if (response.data && response.data.code == "0") {
            if (this.stepNumber === 1) {
              this.$message({
                type: "success",
                message: "草稿编辑成功"
              });
            } else {
              this.stepNumber++;
            }
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("操作失败！");
            }
          }
        })
        .catch(() => {
          this.uploadLoading = false;
          this.$message.error("操作失败！");
        });
    },
    //上一步操作按钮
    lastStepButton() {
      this.stepNumber--;
    },
    //下一步操作按钮
    nextStepButton() {
      if (this.dataValidate(false)) {
        this.stepNumber++;
      }
    },
    //取消编辑操作
    cancelEditButton() {
      this.$confirm("是否确定取消编辑?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "返回",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$emit("tips-event", {
            type: "home-page"
          });
        })
        .catch(() => { });
    },
    //获取数据xhr
    getDataXhr() {
      let param = {
        tipsId: this.baseData.tipsId,
        versionCode: this.baseData.versionCode
      };
      this.$http
        .post("/scene/getTipsDetail", param)
        .then(response => {
          if (
            response.data &&
            response.data.code == "0" &&
            response.data.data
          ) {
            let data = response.data.data
            this.nameData.name = data.name;
            this.nameData.versionCode = data.versionCode;
            this.contentData.time = [data.startTime,data.endTime];
            this.contentData.priority = data.priority;
            this.contentData.displayText = data.displayText;
            this.contentData.iconUrl = data.iconUrl;
            this.textLinkData = data.textLink;
            this.textLinkData.appMinVersion = this.textLinkData.appMinVersion&&parseInt(this.textLinkData.appMinVersion);
            this.noticeData = data.notice?data.notice:{};
            this.noticeDialogVisible = data.notice?true:false;
            this.btnData = data.buttonName?data.buttonLink:{};
            this.btnData.buttonName = data.buttonName;
            this.btnData.appMinVersion = this.btnData.appMinVersion&&parseInt(this.btnData.appMinVersion);
            this.btnDialogVisible = data.buttonName ? true : false;
            this.nowStatus = data.status;
            this.jinyongReason = data.disableReason;
            this.xiajiaReason = data.pulloffReason;

            this.approvalData.approvalTestUserName = data.approvalTestUserName;
            this.approvalData.approvalTestStatus = data.approvalTestStatus;
            this.approvalData.approvalTestReason = data.approvalTestReason;
            this.approvalData.approvalOperationUserName = data.approvalOperationUserName;
            this.approvalData.approvalOperationStatus = data.approvalOperationStatus;
            this.approvalData.approvalOperationReason = data.approvalOperationReason;
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //获取时刻对应的延后时长
    getDisappearTime(date, afterDate) {
      let d1 = new Date(date);
      let d2 = new Date(afterDate);
      let t = d2.getTime() - d1.getTime();
      if (t > 0) {
        return Math.round(t / 60000);
      }
    }
  },
  //初始化请求页面数据
  mounted: function () {
    if (this.isCheckpage || this.isDetailpage) {
      this.isEditPage = false;
      this.stepNumber = 2;
    }
    if (typeof this.baseData.tipsId !== 'undefined') {
      this.getDataXhr();
    }
  },
  created: function () {
    // this.getDataXhr();
  }
};
</script>

